import { useEffect,useState } from "react"
import React from 'react'
import { useParams, Link } from "react-router-dom";
import Search from './Search';
import Stream from './Stream';
import './Episodes.css'


export function Episodes() 
{
    const {id} = useParams();
    const {epParam} = useParams();
    const [episodeList, setEpisodeList] = useState("")
    const [episodeId, setEpisodeId] = useState("")
    const [info, setInfo] = useState("")
    const [selectedEpisode, setSelectedEpisode] = useState("");
    const [isExpanded, setIsExpanded] = useState(false);
    const [loading, setLoading] = useState(true);

useEffect (() =>
  {
    const apiUrl = `https://api-shig.vercel.app/anime/gogoanime/info/${id}`
    fetch(apiUrl)
     .then((response) => response.json())
     .then((data) => 
    {
        const episodeList = data.episodes;
        const info = data;
        setInfo(info);
        setEpisodeList(episodeList);
        if(!epParam)
        {
            setEpisodeId(episodeList[0].id);
        }
        else
        {
            setEpisodeId(epParam);
        }
        //console.log(info);
    })
    .catch(error => console.error("Error", error))
    .finally(()=>{setLoading(false);});
  }, [id])
    

    const handleOnClick = (ep) =>
    {
        setEpisodeId(ep);
        setSelectedEpisode(ep);
    } 
    if (loading) 
    {
        return <div><Search/><Stream/></div>; 
        
    }
    if (!episodeList) 
      {
        return (
         <div>
         <div className='nav'> 
         <Link to={'/'} className='minazuki'>Minazuki</Link>
         </div>
         <div className='error'>
        <img src="\anya.gif" alt="" />
        <p>
          <span className="not-found-text">
            <i1>Oh no!</i1> It seems you've stumbled upon the mysterious <i>404 - Not Found</i> territory. The page you seek is elusive, lost in the digital abyss. Take a moment to retrace your steps or return to the homepage. For any assistance, our support team is ready to guide you through this virtual maze.
          </span>
        </p>
        </div>
        </div>
        );
      }

    return(
        <div>
            <Search/>
            <Stream episodeId={episodeId}/>
            <div className="info">
            <h3>{info.title}</h3>
            {info.description ? (isExpanded ? <p>{info.description}{isExpanded && 
            (
              <button className="show" onClick={() => setIsExpanded(false)}>Show Less</button>
            )}</p> : <p>{info.description.slice(0, 100)}...
            {!isExpanded && 
            (
              <button className="show" onClick={() => setIsExpanded(true)}>See More</button>
            )}
            </p>) : null}
            </div>
            <div className="buttonBox"> 
            <h3>
                Episodes
                <br />
            </h3>
            <div className="buttonContainer">
            {episodeList && episodeList.map((episode, index) =>
            (
               <div key={episode.id}  > 
               <Link to={`/watch/${id}/${episode.id}/`} className={`epButton ${episode.id === episodeId ? 'selected' : ''}`} onClick={()=>handleOnClick(episode.id)}>E{index + 1 } </Link>
               </div>
            )
            )}
            </div>
            </div>
        </div>
    )
}
export default Episodes;
